<template>
    <section class="layout" v-if="error.length === 0">

        <h1 class="title" v-if="$route && $route.name === 'catalog'">{{categoryId}}</h1>
        <h1 class="title" v-if="$route && $route.name === 'search'">"{{searchRequest}}"</h1>

        <div v-if="($route.name === 'catalog' || $route.name === 'catalogWithSub' || isComponent) && categoryId !== null && products.length !== 0"
             class="search-result-wrapper" id="grid" v-bind:style="{'margin-bottom': searchResultWrapperMarginBottom}">

            <Card v-for="item in products" :key="item.id"
                  :id="item.id" :name="item.name"
                  :options="item.options" :children-data="item.childrenData"
                  :button-text="componentData ? componentData.buttonText : 'Заказать'"
                  :basket-data="basketData"
                  v-on:buy="onBuy">
            </Card>

        </div>

        <div v-if=" ($route.name === 'search' || isComponent) && products.length !== 0 && searchRequest !== null"
             class="search-result-wrapper" id="grid" v-bind:style="{'margin-bottom': searchResultWrapperMarginBottom}">

            <Card v-for="item in products" :key="item.id"
                  :id="item.id" :name="item.name"
                  :options="item.options" :children-data="item.childrenData"
                  :button-text="componentData ? componentData.buttonText : 'Заказать' " v-on:buy="onBuy">
            </Card>

        </div>

        <div class="spinner" v-observe-visibility="onBottomReached">
            <LoadingSpinner v-if="isLoading"></LoadingSpinner>
        </div>

        <!--Basket -->
        <Basket v-bind:data="!isComponent ? basket: basketData"
                v-bind:can-be-ordered="!isComponent"
                @ordering="onOrdering" @remove="onRemove">
        </Basket>

    </section>

    <section class="layout" v-else>
        <h1 class="title">{{error}}</h1>
    </section>
</template>

<script>
    import { mapGetters, mapActions, mapMutations } from 'vuex';
    import modules from "../store/all.modules.name"
    import actions from "../store/all.actions.type";
    import mutations from "../store/all.mutations.type";

    import Card from "./components/catalog/Card";
    import LoadingSpinner from "./components/LoadingSpinner";

    import Vue from 'vue'
    import VueObserveVisibility from 'vue-observe-visibility'
    Vue.use(VueObserveVisibility);

    const itemsPerPage = 9;

    const ERR_WHILE_LOADING_DATA = "Ошибка загузки данных с сервера.";

    export default {
        name: "Catalog",

        components: {
            LoadingSpinner,
            Basket: () => import("./components/catalog/Basket"),
            Card},

        data: () => {
            return {
                isLoading: true,
                hasMoreToLoad: false,
                lastPageNumber: 1,

                products: [],

                error: '',
            }
        },

        props: {
            isComponent: {
                type: Boolean,
                default: false,
            },

            componentData: {
                type: Object,
                default: null,
            },

            basketData: {
                type: Array,
                default: null,
            },
        },

        methods: {
            ...mapActions(modules.catalog, [
                actions.catalog.ADD_TO_BASKET,
                actions.catalog.GET_PRODUCTS,
                actions.catalog.GET_MORE_PRODUCTS,
                actions.catalog.SEARCH,
            ]),

            ...mapMutations(modules.catalog, [
                mutations.catalog.REMOVE_FROM_BASKET,
                mutations.catalog.SET_IS_BASKET_MINIMIZED,
            ]),

            init() {
                this.isLoading = true;

                if (this.searchRequest) {
                    this.search(this.searchRequest).then((response) => {
                        this.hasMoreToLoad = false;

                        if ('data' in response) {
                            this.products = response.data;
                        } else if ('err' in response) {
                            this.error = ERR_WHILE_LOADING_DATA
                        }
                    })
                } else if (this.categoryId) {
                    let payload;
                    if (this.subCategoryId) {
                        payload = {categoryId: this.categoryId, subCategoryId: this.subCategoryId, itemsPerPage};
                    } else {
                        payload = {categoryId: this.categoryId, itemsPerPage};
                    }

                    this.getProducts(payload).then((response) => {
                        if ('data' in response) {
                            if (response.data.length === itemsPerPage) {
                                this.hasMoreToLoad = true;
                            }
                            this.products = response.data;

                        } else if ('err' in response) {
                            this.hasMoreToLoad = false;
                            this.error = ERR_WHILE_LOADING_DATA;
                        }
                    });
                }

                this.isLoading = false;
            },

            onBuy(item) {
                this.setIsBasketMinimized(false);

                if (!this.isComponent) {
                    this.addToBasket(item)
                } else {
                    this.$emit('buy', item);
                }
            },

            onOrdering() {
                this.$router.push({name: "ordering"})
            },

            onRemove(index) {
                if (!this.isComponent) {
                    this.removeFromBasket(index);
                } else {
                    this.$emit('basketRemove', index)
                }
            },

            onBottomReached(isVisible) {
                if (!this.isLoading && this.hasMoreToLoad && this.error.length === 0 && isVisible && this.categoryId) {
                    this.isLoading = true;
                    this.lastPageNumber++;

                    this.getMoreProducts(
                        {
                            categoryId: this.categoryId,
                            subCategoryId: this.subCategoryId,
                            pageNumber: this.lastPageNumber,
                            itemsPerPage: 9,
                        })
                        .then((payload) => {
                            let {data, hasMore} = payload;
                            this.hasMoreToLoad = hasMore;
                            this.products = [...this.products, ...data];
                            this.isLoading = false;
                        })
                }
            },
        },

        computed: {
            ...mapGetters(modules.catalog, [
                "basket",
            ]),

            searchResultWrapperMarginBottom: function() {
                if (this.isBaskeWindowVisible) {
                    return 230 + "px"
                } else {
                    return 0 + "px"
                }
            },

            categoryId: function () {
                if (this.$route && this.$route.params && (this.$route.name === "catalog" || this.$route.name === "catalogWithSub")) {
                    return this.$route.params.categoryId;
                } else if (this.$props.componentData && this.$props.componentData.category && this.$props.componentData.category.length !== 0) {
                    return this.$props.componentData.category;
                } else {
                    return null;
                }
            },

            subCategoryId: function () {
                if (this.$route !== null && this.$route.params !== null && this.$route.name === "catalogWithSub") {
                    return this.$route.params.subCategoryId;
                } else if (this.$props.componentData && this.$props.componentData.subCategory && this.$props.componentData.subCategory.length !== 0) {
                    return this.$props.componentData.subCategory;
                } else {
                    return null;
                }
            },

            searchRequest: function () {
                if (this.$route !== null && this.$route.params !== null && this.$route.name === "search") {
                    return this.$route.params.request;
                } else if (this.$props.componentData && this.$props.componentData.searchRequest && this.$props.componentData.searchRequest.length !== 0){
                    return this.$props.componentData.searchRequest
                } else {
                    return null
                }
            },
        },

        watch: {
            '$route': {
                handler: function() {
                    this.init();
                },
                deep: true,
                immediate: true
            }
        },

        mounted() {
            //this.init();
        },

        destroyed() {
        }
    }
</script>

<style lang="sass" scoped>
    @import '../assets/basic'

    .layout
        width: 90%!important
        margin: auto
        max-width: 1200px!important
        font-family: Roboto, sans-serif

        .title
            text-align: center
            font-size: 1.8rem
            font-weight: normal
            font-family: Oswald, sans-serif

        .spinner
            display: flex
            justify-content: center
            margin-top: 2rem


        .search-result-wrapper
            display: grid
            grid-gap: 15px
            grid-template-columns: repeat( auto-fill, minmax(250px, 1fr) )

            /*@include mobile
                grid-template-columns: 1fr

            /*@include tablet
                grid-template-columns: repeat(2, 1fr)

            /*@include laptop
                grid-template-columns: repeat(3, 1fr)

            /*@include laptop-l
                grid-template-columns: repeat(4, 1fr)
</style>